import React from "react";
import Preload from "react-preload";
import { Controller, Scene } from "react-scrollmagic";

import "./loader.js";
import $ from "jquery";

import logo from "./logo.svg";
import "./App.css";

import InitialPage from "./pages/initialPage";
import Home from "./pages/home";
import Register from "./pages/register";
import Welcome from "./pages/welcome/index.js";
import Marketplace from "./pages/marketplace/MarketBody.js";
import Partners from "./pages/partners/index.js";
import Subscribe from "./pages/subscribe/index.js";
import Footer from "./pages/footer/index.js";

const imgBase = "https://picsum.photos/800/600?";

function App() {
  const images = [
    `${imgBase}/${Date.now()}`,
    `${imgBase}/${Date.now()}`,
    `${imgBase}/${Date.now()}`,
  ];

  const [loading, setLoading] = React.useState(false);

  const onSuccess = () => {
    setLoading(true);
    $(".loading-aon-image").addClass("loading-disappear");
    $(".loading-images").addClass("display-none");
  };

  const loadingIndicator = <InitialPage />;
  return (
    <div className="App">
      <div className="home">
        <Preload
          loadingIndicator={loadingIndicator}
          images={images}
          // autoResolveDelay={5000}
          onError={onSuccess}
          onSuccess={onSuccess}
          resolveOnError={true}
          mountChildren={true}
        >
          <Home />
        </Preload>
      </div>
      {loading ? (
        <>
          <Register />
          <Welcome />
          <Marketplace />
          <Partners />
          <Subscribe />
          <Footer />
        </>
      ) : (
        ""
      )}
    </div>
  );
}

export default App;
