import React from "react";

const AonImage = ({
  src,
  className = null,
  id = null,
  style = null,
  visible = true,
}) => {
  const classnames = visible ? className : className + " display-none";

  const image = id ? (
    <img id={id} src={src} className={classnames} />
  ) : (
    <img src={src} className={classnames} />
  );

  return image;
};

export default AonImage;
